.slider {
    height: 150px;
    margin: auto;
    position:relative;
    width: 90%;
    display: grid;
    place-items: center;
    overflow: hidden;
}

.slider::before,
.slider::after {
    background: linear-gradient(to right, rgba(255,255,255,1) 0%, rgba(255,255,255,0) 100%);
    content: "";
    height: 100%;
    position: absolute;
    width: 15%;
    z-index: 2;
}

.slider::before {
    left:0;
    top:0;
}
.slider::after {
    right:0;
    top:0;
    transform: rotateZ(180deg);
}


.slide-tracker {
    display: flex;
    width: calc(250px * 10);
    animation: scroll 40s linear infinite;
}
.slide-tracker:hover {
    animation-play-state: paused;
}

.slide {
    height: 200px;
    width: 250px;
    display: flex;
    align-items: center;
    padding: 15px;
    background-color: rgb(242, 242, 248);
    perspective: 100px;
}

.slide img {
    width: 100%;
    /* height: 125px; */
    transition: transform 1s;
}

.slide img:hover {
    transform: translateZ(20px);
}

@keyframes scroll {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(calc(-250px *5));
    }
}

/* ::shadow */
.slider::before,
.slider::after {
    background: linear-gradient(to right, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 100%);
    content: "";
    height: 100%;
    position: absolute;
    width: 15%;
    z-index: 2;
}

.slider::before {
    left: 0;
    top: 0;
}

.slider::after {
    right: 0;
    top: 0;
    transform: rotateZ(180deg);
}

